.app__chef-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}

.app__chef-content_quote {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}

.app__chef-content_quote img {
  width: 47px;
  height: 40px;
  margin: 0 1rem 1rem 0;
}

.app__chef-content p {
  color: var(--color-grey);
  font-style: italic;
}

.app__chef-sign {
  width: 100%;
  margin-top: 3rem;
}

.app__chef-sign p:first-child {
  font-family: var(--font-base);
  font-size: 32px;
  line-height: 42px;
  letter-spacing: 0.04em;
  font-weight: 400;
  text-transform: capitalize;
  color: var(--color-golden);
}

.app__chef-sign img{
  width: 250px;
  margin-top: 3rem;
}

/* Very Large Devices ======================== */
@media screen and (min-width: 2000px) {
  .app__chef-sign img{
    width: 370px;
  }
  .app__chef-sign p:first-child {
    font-size: 46px;
    line-height: 73px;
  }
}

/* Small Devices ============================= */
@media screen and (max-width: 450px) {
  .app__chef-sign img{
    width: 80%;
  }
}